body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* @font-face {
  font-family: 'homemade';
  src: local('homemade'), url(./fonts/Homemade.ttf) format('truetype');
} */
@font-face {
  font-family: 'SyneMono';
  src: local('SyneMono'), url(./fonts/SyneMono-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Quicksand';
  src: local('Quicksand'), url(./fonts/Quicksand.ttf) format('truetype');
  font-weight: 500;
}
@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'), url(./fonts/Montserrat-Regular.ttf) format('truetype');
  font-weight: 500;
}
