.App {
  background-color: #373a3d;
  color: white;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
body{
  background-color: #373a3d;
}

h3{
  font-family: 'SyneMono';
  color: white;  
}
h5, h6, h2, h1, small{
  font-family: 'Montserrat';  
}
h2{
  font-size: 1rem;
  color: white
}
p, li, a, button, tr{
  font-family: 'Quicksand';
  color :#c7c7c7;
}

small{
  display: block;
}
ul {
    list-style-type: none;
    padding-inline-start: 15px;
}
li{
  padding-bottom: 1.5em;
}
a{
  color: #9effab;
  text-decoration: none;
}
.primaryBG{
  background-color: #373a3d;
}
.secondBG{
  background-color: #373a3d;
}
.thirdBG{
  background-color: #373a3d;
}
.primaryF{
  color: #c7c7c7!important;
}
.secondF{
  color: #9effab!important;
}
.thirdF{
  color: #373a3d!important;
}


@keyframes thover{
  0%{ 
    color: #9effab;
    text-decoration: none;
  }

  100%{
    color:#9effab;
    text-shadow:0 0 10px #b6ffca;
  }
}
a:hover{
  color:#9effab;
  text-shadow:0 0 10px #b6ffca;
  text-decoration: none;
}
input, textarea{ 
  background-color: rgba(0, 0, 0, 0);
  color: #c7c7c7;
  font-family: "Quicksand";
  padding: 5px;
  border: 1px solid #c7c7c7;
}
textarea{
  width: 390px;
  height: 200px
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

@media only screen and (max-width: 1410px) {
  .foothide {
    display: none;
  }
}
@media only screen and (max-width: 420px) {
  .logo {
    display: none;
  }
  .links {
    justify-content: space-around!important;
  }
}
.hold{
  color:  #a6ffbd;
  background-color: #39ff6e;
}
.tStyle {
  flex: 1 1 auto;
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 25px;
  height: 100%;
  display: flex;
  flex-flow: column;
}
.w100{
 width: 100%;
}
.butn{
  border: none;
  padding: 10px 25px;
  font-weight: 900;
}
.butn-hot{
  background-color: #9effab;
  color: #373a3d;
}
.butn-not{
  background-color: #373a3d;
  color:#9effab;
  border: 4px solid #9effab;
}
.message-box {
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  padding: 15px;
}
.from{
  margin: 10px 30px 10px 10px;
  background-color: #9effab;
  color: #373a3d;
  align-self: flex-start;
  align-items: flex-start;
}
.from>p{
  color: #373a3d!important;
}
.to {
  align-items: flex-end;
  align-self: flex-end;
  margin: 10px 10px 10px 30px;
  background-color: #373a3d;
}
.errorMessage{
  color: #ff89f9;
}
.centered {
  display: flex;
  align-items: center;
  justify-content: center;
}
.fader{
  opacity: 0.2;
  animation: fader 0.3s;
}
@keyframes fader{
  from{
    opacity: 1;
  }
  to{
    opacity: 0.2;
  }
}
@keyframes tag{
  0%{ 
    color: white;
    text-shadow: none;
  }

  100%{
    color:#9effab;
    text-shadow:0 0 10px #b6ffca;
  }
}
.headlinks:hover{
  animation: tag 0.5s;
  color:#9effab;
  text-shadow:0 0 10px #b6ffca;  
}
.modal-content{
  border: 1px solid #c7c7c7!important
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;               /* width of the entire scrollbar */
}
::-webkit-scrollbar-track {
  background: #373a3d;   /* color of the tracking area */
}
::-webkit-scrollbar-thumb {
  background-color: #656c70;    /* color of the scroll thumb */
  color: #1e2024; 
  width: 4px;      /* roundness of the scroll thumb */
  /* creates padding around scroll thumb */
}